.cwm-tab-nav {
    display: flex;
    justify-content: flex-start;
    gap: 2px;
    margin-bottom: 10px;
    width: 100%;
    // background: #a1a1a1;
    padding: 2px 0;
    border-top: 1px dashed #6d6d6d;
    border-bottom: 1px dashed #6d6d6d;
}
.cwm-tab-nav li {
    list-style: none;
    padding: 5px 10px;
    background: #f1f1f1;
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-bottom: 0;
    transition: all 0.3s ease-in-out;
}
.cwm-tab-nav li:hover {
    background: #a1a1a1;
    color: white;
}

.cwm-tab-nav li.active {
    background: #0073aa;
    color: white;
}
.cwm-tab-nav li.has-settings:after {
    font-family: dashicons;
    font-size: 120%;
    padding-left: 4px;
}

.cwm-tab-nav li.has-settings.setting-show:after {
    content: "\f177";
}

.cwm-tab-nav li.has-settings.setting-hide:after {
    content: "\f530";
}

.cwm-tab-content {
    margin-bottom: 15px;
}

select.cwm-mode {
    margin-bottom: 10px;
}

input.cwm-widget-classes {
    width: 100%;
    margin-bottom: 10px;
}
